<style>
  .p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .img-w {
    width: 80px;
    height: 80px;
  }
  .img-w>img {
    width: 80px;
  }
  .p-m-c-r {
    padding: 0 10px;
  }
  .p-m-c-r .el-button {
    user-select: text !important;
    -webkit-user-select: text !important;
    -ms-user-select: text !important;
    -moz-user-select: text !important;
  }
  .erp-p-no {
    font-size: 12px !important;
  }
  .g-m-r {
    position: relative;
  }
  .mt-show-w {
    width: 120px;
    height: 120px;
    position: relative;
  }
  .mt-show-w>img {
    width: 120px;
  }
  .t-m-w, .b-m-w {
    height: 24px;
    background: rgba(0, 0, 0, .5);
    text-align: center;
    line-height: 24px;
    font-size: 10px;
    color: #fff;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  .t-m-w {
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
  }
  .b-m-w {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
  .red-tip {
    color: red;
    cursor: pointer;
  }
  .g-no-w, .order-qty-txt {
    cursor: pointer;
  }
  .g-no-w:hover, .order-qty-txt:hover {
    color: #3080fe;
  }
</style>

<template>
  <div>
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="showResearchDialog" size="mini" icon="el-icon-search">重新查询要货汇总</el-button>
        <span style="color: red;">注意：每天8:00-22:00之间，请不要做此操作，避免重新同步库存，导致线上超卖!</span>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="商品分类">
            <el-cascader
              placeholder="请选择分类"
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doCategoryChangeHandel"
              :props="cateDefaultProps"
              v-model="searchContent.categoryIdList">
            </el-cascader>
          </el-form-item>
          <el-form-item label="SKU编码/商品名称">
            <goods-select ref="GoodsSelect" @DoSelectGoodsHandel="DoSelectGoodsHandel"></goods-select>
          </el-form-item>
          <el-form-item label="订货开始日期">
            <el-date-picker class="search-input" v-model="searchContent.orderDateFrom" type="date" placeholder="请选择订货开始日期" format="yyyy-MM-dd" value-format="yyyy-MM-dd" size="small" @change="doSearchHandel(true)">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="订货结束日期">
            <el-date-picker class="search-input" v-model="searchContent.orderDateTo
            " type="date" placeholder="请选择订货结束日期" format="yyyy-MM-dd" value-format="yyyy-MM-dd" size="small" @change="doSearchHandel(true)">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="同步状态">
            <el-select class="search-input" v-model="searchContent.syncStatus" filterable placeholder="请选择同步状态" clearable size="small" @change="doSearchHandel(true)">
              <el-option
                v-for="item in syncStatusOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
           </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    
    <div class="table-wraper">
      <div class="batch-handel-w">
        <el-button class="sup-mini" size="mini" icon="el-icon-sort" :disabled="checkedList.length === 0" @click="synchronizeHandel">同步</el-button>
      </div>
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        @select="handleSelect"
        @select-all="handleSelectAll"
        size="small">
        <el-table-column
          :selectable="handleSelection"
          type="selection"
          width="50">
        </el-table-column>
        <el-table-column min-width="110px" prop="orderDate" label="订货日期"></el-table-column>
        <el-table-column min-width="236px" label="商品信息">
          <template slot-scope="scope">
            <div class="p-m-w">
              <div class="img-w g-m-r">
                <p class="t-m-w" v-show="scope.row.topMark">{{scope.row.topMark}}</p>
                <img v-if="scope.row.goodsImg && scope.row.goodsImg != ''" :src="scope.row.goodsImg" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
                <p class="b-m-w" v-show="scope.row.bottomMark">{{scope.row.bottomMark}}</p>
              </div>
              <div class="p-m-c-r">
                <div>{{scope.row.goodsName || '--'}}</div>
                <div>
                  <el-popover
                    placement="bottom-start"
                    trigger="click">
                    <el-table
                      :data="skuListMsg"
                      empty-text="暂无数据"
                      size="small">
                      <el-table-column width="80" prop="skuNo" label="ERP编码"></el-table-column>
                      <el-table-column width="140" prop="skuName" label="母商品名称"></el-table-column>
                      <el-table-column width="90" prop="erpCategoryName" label="ERP分类"></el-table-column>
                      <el-table-column width="90" prop="skuType" label="商品种类" :formatter="skuTypeFormatter"></el-table-column>
                      <el-table-column width="80" prop="ratio" label="转换比例"></el-table-column>
                      <el-table-column width="60" prop="unit" label="单位"></el-table-column>
                    </el-table>
                    <el-button slot="reference" type="text" size="mini" @click="showSkuMsg(scope.row)" class="erp-p-no g-no-w">SKU编码：{{scope.row.goodsNo || '--'}}</el-button>
                  </el-popover>
                </div>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="100px" prop="goodsSpec" label="规格"></el-table-column>
        <el-table-column min-width="140px" prop="orderQty" :render-header="orderQtyHeader">
          <template slot-scope="scope" size="small">
            <div v-if="scope.row.orderQty" class="order-qty-txt">
              <el-popover
                placement="bottom-start"
                trigger="click">
                <div>
                  <div>汇总单号：{{transferTotalPaperNo || '--'}}</div>
                  <div>汇总时间：{{transferTotalTime || '--'}}</div>
                  <div v-for="(item, index) in transferGoodsList" :key="index">
                    {{item.skuNo}}&nbsp;&nbsp;&nbsp;{{item.skuName}}&nbsp;&nbsp;&nbsp;{{item.orderQty}}&nbsp;&nbsp;&nbsp;【{{item.skuSpec}}】
                  </div>
                </div>
                <el-button slot="reference" type="text" size="mini" @click="orderQtyMsg(scope.row)">{{scope.row.orderQty}}</el-button>
              </el-popover>
            </div>
            <div v-else>{{scope.row.orderQty || '--'}}</div>
          </template>
        </el-table-column>
        <el-table-column min-width="90px" prop="stockQty" :render-header="stockQtyHeader" :formatter="emptyFormatter"></el-table-column>
        <el-table-column min-width="110px" prop="safeStockRatio" label="安全库存占比" :formatter="emptyFormatter"></el-table-column>
        <el-table-column min-width="100px" prop="safeStockQty" :render-header="safeStockQtyHeader" :formatter="emptyFormatter"></el-table-column>
        <el-table-column min-width="166px" prop="syncDateTime" label="同步时间" :formatter="emptyFormatter"></el-table-column>
        <el-table-column min-width="90px" label="同步状态">
          <template slot-scope="scope" size="small">
            <div>
              <div v-if="scope.row.syncStatus === 0" >待同步</div>
              <div v-else-if="scope.row.syncStatus === 1" >同步中</div>
              <div v-else-if="scope.row.syncStatus === 2" >同步成功</div>
              <div v-else-if="scope.row.syncStatus === 3" class="red-tip" @click="showErrMsg(scope.row)">同步失败</div>
              <div v-else-if="scope.row.syncStatus === 4" >不同步</div>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="100">
          <template slot-scope="scope" size="small">
            <div>
              <el-button v-if="scope.row.syncStatus === 3 && scope.row.canRetry" @click="reSynchronizeHandel(scope.row)" type="text" size="mini" icon="el-icon-sort">重新同步</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible" width='460px'>
      <el-form v-if="dialogFrom === 'research'">
        <el-form-item label="选择日期">
          <el-select class="search-input" v-model="dateTag" filterable placeholder="请选择选择日期" size="small" @change="dateTagChange">
            <el-option
              v-for="item in dateTagOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
         </el-form-item>
      </el-form>

      <div v-if="dialogFrom === 'errMsg'">
        {{syncErrMsg}}
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false" size="mini">关 闭</el-button>
        <el-button type="primary" @click="researchHandel" :disabled="isDisabled" size="mini" v-if="dialogFrom === 'research'" :loading="isDisabled">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import GoodsSelect from "@/components/Common/GoodsSelectByPid"
  const reqUrls = {
    // 获取三方平台店内分类
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 列表
    stockList: '/plateform/a/sf/stock/list',
    // 重新同步
    retrySync: '/plateform/a/sf/stock/retrySync',
    // 重新查询要货汇总
    retryTransferTotal: '/plateform/a/sf/stock/retryTransferTotal'
  }
  export default {
    data() {
      return {
        isDisabled: false,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          saleType: 1,
          platformId: 6,
          queryString: '', // 查询条件
          platformCityGoodsIds: [], // 平台区域商品ID
          categoryId: '', 
          goodsNoList: [], // 商品编码
          categoryIdList: [],
          orderDateFrom: '', 
          orderDateTo: '',
          syncStatus: ''
        },
        // 同步状态 options
        syncStatusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '待同步',
            value: 0
          },
          {
            label: '同步中',
            value: 1
          },
          {
            label: '同步成功',
            value: 2
          },
          {
            label: '同步失败',
            value: 3
          },
          {
            label: '不同步',
            value: 4
          }
        ],
        // 选择日期  默认今日
        dateTag: 1,
        erpTotalDate: utils.timestampToTimeNoHMS(Date.parse(new Date())), 
        dateTagOptions: [
          {
            label: '今日',
            value: 1
          },
          {
            label: '昨日',
            value: 2
          }
        ],
        // 默认分类参数
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        categoryList: [], // 商品分类id
        tableData: [], // 表格中查询结果数据
        // 多选选中的商品
        checkedList: [],

        dialogTitle: '重新查询要货汇总', // 同步失败原因   重新查询要货汇总
        formLabelWidth: '80',
        dialogFormVisible: false, // 是否显示
        dialogFrom: 'research', // research 重新查询要货汇总操作 和 errMsg 同步失败原因操作
        // 失败原因
        syncErrMsg: '',
        // 母商品信息
        skuListMsg: [],

        transferTotalPaperNo: '', // 汇总单号
        transferTotalTime: '', // 汇总时间
        // 汇总商品
        transferGoodsList: ''
      }
    },
    created() {
      // 三方平台店内分类
      this.getCategoryId()
    },
    mounted() {
      this.doSearchHandel()
    },
    components: {
      "goods-select": GoodsSelect
    },
    methods: {
      orderQtyHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover"
            content="组合商品时，取转化线上库存最少的商品要货数量。">
            <span slot="reference">母商品要货数量<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      stockQtyHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover"
            content="库存 = 母商品要货数量 * 转换比例；多个母商品时取最小库存数。">
            <span slot="reference">库存<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      safeStockQtyHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover"
            content="安全库存 = 库存 * 安全库存占比。">
            <span slot="reference">安全库存<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      emptyFormatter(row, column, cellValue) {
        if(!cellValue && cellValue !== 0) {
          return '--'
        }else {
          return cellValue
        }
      },
      /**
       * [skuTypeFormatter 表格数据格式化]
       */
       skuTypeFormatter(row, column, cellValue) {
        if(cellValue === 'C') {
          return "组合商品"
        }else if(cellValue == 'Q') {
          return "计量商品"
        }else if(cellValue == 'S') {
          return "标准商品"
        }else if(cellValue == 'W') {
          return "重量商品"
        }else {
          return "--"
        }
      },
      handleSelection (row, index) {
        return row.canRetry
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(reqUrls.getCategoryTree).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [doCategoryChangeHandel 分类发生变化]
       * @return {[type]} [description]
       */
       doCategoryChangeHandel() {
        // 商品筛选组件初始化
        this.goodsSelectInit()

        this.doSearchHandel(true)
      },
      /**
       * [goodsSelectInit 商品筛选组件初始化]
       * @return {[type]} [description]
       */
       goodsSelectInit() {
        if(this.$refs.GoodsSelect) {
          let _val = {
            queryString: this.searchContent.queryString,
            categoryIdList: this.searchContent.categoryIdList,
            saleType: this.searchContent.saleType,
            platformId: this.searchContent.platformId,
            platformCityGoodsIds: this.searchContent.platformCityGoodsIds
          }
          this.$refs.GoodsSelect.setDefaultMesg(_val)
        }
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandels 搜索]
       * @param  {[type]} _params [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        // 将选中数据重置为初始值
        this.checkedList = []
        // categoryId 为最末级id
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        let beginTimeVal = this.searchContent.orderDateFrom ? new Date(this.searchContent.orderDateFrom.replace(/-/g, '/')).getTime() : 0
        let endTimeVal = this.searchContent.orderDateTo ? new Date(this.searchContent.orderDateTo.replace(/-/g, '/')).getTime() : 0
        if (this.searchContent.orderDateFrom && this.searchContent.orderDateTo && !isNaN(beginTimeVal) && !isNaN(endTimeVal)) {
          if (beginTimeVal > endTimeVal) {
            this.$message.warning("订货结束日期不能早于开始日期")
            return
          }
        }
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        let queryS = {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          categoryId: this.searchContent.categoryId,
          goodsNoList: this.searchContent.goodsNoList,
          orderDateFrom: this.searchContent.orderDateFrom ? `${this.searchContent.orderDateFrom} 00:00:00` : '',
          orderDateTo: this.searchContent.orderDateTo ? `${this.searchContent.orderDateTo} 23:59:59` : '',
          syncStatus: this.searchContent.syncStatus
        }
        this.$http.post(reqUrls.stockList, { ...queryS }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data) {
              if(res.data.result && res.data.result.length) {
                this.tableData = res.data.result
              }else {
                this.tableData = []
              }
              this.total = res.data.total
            }else {
              this.total = 0
              this.tableData = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '系统提示',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
       handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.id, 'id', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('id', row.id, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.id] ? '' : hash[next.id] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.id] ? '' : hash[next.id] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'id')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * @description: 重新查询要货汇总
       * @param {type} 
       * @return: 
       */
      showResearchDialog() {
        this.dialogTitle = '重新查询要货汇总'
        this.dialogFormVisible = true
        this.dialogFrom = 'research'
        this.dateTag = 1
        this.erpTotalDate = utils.timestampToTimeNoHMS(Date.parse(new Date()))
      },
      dateTagChange(e) {
        if(e === 1) {
          this.dateTag = 1
          this.erpTotalDate = utils.timestampToTimeNoHMS(Date.parse(new Date()))
        }else if(e === 2) {
          this.dateTag = 2
          this.erpTotalDate = utils.timestampToTimeNoHMS(Date.parse(new Date()) - 86400000)
        }
      },
      /**
       * @description: 重新查询要货汇总
       * @param {type} 
       * @return: 
       */
      researchHandel() {
        this.isDisabled = true
        this.$http.post(reqUrls.retryTransferTotal, { erpTotalDate: this.erpTotalDate }).then(res => {
          if(res.code === 0) {
            this.dialogFormVisible = false
            setTimeout(() => {
              this.isDisabled = false
            }, 500);
            this.doSearchHandel(true)
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          setTimeout(() => {
            this.isDisabled = false
          }, 500);
          this.$notify.error({
            title: '系统提示',
            message: e.message
          })
        })
      },

      /**
       * @description: 批量同步操作
       * @param {type} 
       * @return: 
       */
      synchronizeHandel() {
        let _this = this
        if(this.checkedList.length === 0) {
          this.$message.warning("请先选择要同步的商品")
          return
        }
        let ids = this.checkedList.map(item => {
          return item.id
        })
        this.$confirm('确定要同步所选商品吗?', '温馨提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          _this.$http.post(reqUrls.retrySync, ids).then(res => {
            if(res.code === 0) {
              this.$message.success("操作成功！")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            _this.$notify.error({
              title: '系统提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },

      /**
       * @description: 查看同步失败原因
       * @param {type} 
       * @return: 
       */
      showErrMsg(_row) {
        this.dialogTitle = '同步失败原因'
        this.dialogFormVisible = true
        this.dialogFrom = 'errMsg'
        // 失败原因
        this.syncErrMsg = _row.syncErrMsg
      },
      /**
       * @description: 重新同步
       * @param {type} 
       * @return: 
       */
      reSynchronizeHandel(_row) {
        let _this = this
        this.$confirm('确定要重新同步吗?', '温馨提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let ids = []
          ids.push(_row.id)
          _this.$http.post(reqUrls.retrySync, ids).then(res => {
            if(res.code === 0) {
              this.$message.success("操作成功！")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            _this.$notify.error({
              title: '系统提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * @description: 显示母商品信息
       * @param {type} 
       * @return: 
       */
      showSkuMsg(_row) {
        this.skuListMsg = _row.skuList || []
      },
      /**
       * @description: 鼠标移到母商品要货数量显示数据
       * @param {type} 
       * @return: 
       */
      orderQtyMsg(_row) {
        // 汇总单号
        this.transferTotalPaperNo = _row.transferTotalPaperNo || ''
        // 汇总时间
        this.transferTotalTime = _row.transferTotalTime || ''
        // 商品
        this.transferGoodsList = _row.skuList || []
      },
      /**
       * [DoSelectGoodsHandel 确认选择]
       */
       DoSelectGoodsHandel(_params) {
        let checkedList = _params.checkedList
        let queryString = _params.queryString
        let platformCityGoodsIds = []
        let goodsNoListTemp = []
        for(let item of checkedList) {
          platformCityGoodsIds.push(item.platformCityGoodsId)
          goodsNoListTemp.push(item.goodsNo)
        }
        this.searchContent.platformCityGoodsIds = platformCityGoodsIds
        this.searchContent.goodsNoList = goodsNoListTemp
        this.searchContent.queryString = queryString
        this.doSearchHandel(true)
      }
    }
  }
</script>


